/*
* Typography
*/
body {
  color: #333333;
  font-size: 18px;
  font-family: "Noto Sans", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
a,
ul,
li,
ol,
textarea,
input[type] {
  font-family: "Noto Sans", sans-serif;
}

h1 {
  font-size: 2.25em;
  line-height: 2.25em;
}

h2 {
  font-size: 2em;
  line-height: 2em;
}

h3 {
  font-size: 1.75em;
  line-height: 1.75em;
}

h4 {
  font-size: 1.5em;
  line-height: 1.75em;
}

p {
  line-height: 1.5em;
}

a {
  color: goldenrod;
  text-decoration: none;
}

a:hover {
  color: #55accc;
  transition: 0.4s color ease;
}

.button {
  border: 3px solid #000000;
  color: #000000;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  padding: 0.5em 0.75em;
  transition: all 0.5s ease;
}

.button:hover {
  background-color: #000000;
  color: #ffffff;
  transition: all 0.5s ease;
}

.button-blue {
  border: 3px solid #55accc;
  color: #55accc;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  padding: 0.5em 0.75em;
  transition: all 0.5s ease;
}

.button-blue:hover {
  background-color: #55accc;
  color: #ffffff;
  transition: all 0.5s ease;
}

/*
* General Body Styles
*/
.body-wrapper {
  position: relative;
  left: 0;
  transition: 0.5s left ease;
}

.body-wrapper.slide {
  left: 50%;
  transition: 0.5s left ease;
}

#maincontent {
  margin: 0 auto;
  max-width: 1440px;
  padding: 0 1em;
}

#maincontent .blog, #maincontent .archive {
  margin: 1em 0;
}

/* 
* Header
*/
#header {
  background-color: #333333;
  box-shadow: 0 0px 20px black;
  border-bottom: 2px solid goldenrod;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}

#header.animate {
  animation: header-slide-left 1s ease 1s forwards;
  opacity: 0;
}

#header .header-container {
  margin: 0 auto;
  max-width: 1440px;
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 0.5em 1em;
}

#header .header-container .title {
  border-bottom: 5px solid goldenrod;
  border-top: 5px solid goldenrod;
  color: #eeeeee;
  line-height: normal;
  text-align: center;
}

#header .header-container .title.resized {
  border-width: 2px;
  font-size: 1em;
  transition: all 0.5s ease;
}

#header .header-container .title a {
  color: #eeeeee;
}

#header .menu li {
  display: inline-block;
}

#header .menu li a {
  color: #eeeeee;
}

#header .menu li a:hover {
  color: #55accc;
}

#header .menu li:not(:first-child) a {
  padding-left: 0.5em;
}

#header .menu li:not(:last-child) a {
  padding-right: 0.5em;
}

#header .desktop-menu {
  display: none;
}

@media screen and (min-width: 768px) {
  #header .desktop-menu {
    display: block;
  }
}

#header .mobile-toggle {
  color: goldenrod;
  font-size: 2em;
  transition: 0.4s ease;
}

#header .mobile-toggle:hover {
  color: #55accc;
  cursor: pointer;
  transition: 0.4s ease;
}

@media screen and (min-width: 768px) {
  #header .mobile-toggle {
    display: none;
  }
}

#mobile-nav {
  background-color: #000000;
  box-shadow: 0 0 50px #000000;
  height: 100vh;
  padding: 2em 0;
  position: fixed;
  left: -50%;
  text-align: center;
  top: 0;
  transition: 0.5s left ease;
  width: 50%;
  z-index: 20;
}

#mobile-nav.show {
  left: 0;
  transition: 0.5s left ease;
}

#mobile-nav a {
  display: block;
  padding: 1em 0;
  transition: 0.4s background-color ease;
}

#mobile-nav a:hover {
  background-color: #4d4d4d;
  color: goldenrod;
  transition: 0.4s background-color ease;
}

/* 
* Footer
*/
#footer {
  background-color: #333333;
  box-shadow: inset 0 0 30px #000000;
  padding: 2em 0;
  width: 100%;
}

#footer .footer-container {
  margin: 0 auto;
  max-width: 1440px;
  background-color: #262626;
  box-shadow: 0 0 30px #000000;
  padding: 2em 1em;
  text-align: center;
}

#footer .title {
  border-bottom: 5px solid goldenrod;
  border-top: 5px solid goldenrod;
  display: table;
  line-height: normal;
  margin: 0 auto;
  text-align: center;
}

#footer .title a {
  color: #eeeeee;
}

#footer p {
  color: #eeeeee;
  margin: 1em 0;
}

#footer .icon-wrapper .icon-container {
  background-color: #55accc;
  border-radius: 50%;
  display: inline-block;
  height: 65px;
  margin: 0 1em;
  transition: 0.4s background-color ease;
  width: 65px;
}

#footer .icon-wrapper .icon-container:hover {
  background-color: goldenrod;
  transition: 0.4s background-color ease;
}

#footer .icon-wrapper .icon-container i {
  color: #000000;
  font-size: 3em;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.home .homepage-landing {
  left: 50%;
  margin-left: -50vw;
  position: relative;
  width: 100vw;
  animation: fade-in 1s ease;
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../images/f14.jpg");
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  display: table;
  height: 100vh;
  overflow: hidden;
}

.home .homepage-landing .landing-title {
  animation: landing-fade-slide 1s ease 1s forwards;
  color: #eeeeee;
  display: table-cell;
  font-size: 8vw;
  opacity: 0;
  position: relative;
  text-align: center;
  vertical-align: middle;
}

@media screen and (min-width: 768px) {
  .home .homepage-landing .landing-title {
    font-size: 6vw;
  }
}

.home .about-me-wrapper {
  left: 50%;
  margin-left: -50vw;
  position: relative;
  width: 100vw;
  background-color: #55accc;
  padding: 2em 0;
}

.home .about-me-wrapper .about-me-container {
  margin: 0 auto;
  max-width: 1440px;
  opacity: 0;
  transform: translateY(20%);
}

.home .about-me-wrapper .about-me-container:after {
  clear: both;
  content: '';
  display: table;
}

.home .about-me-wrapper .about-me-container.show {
  opacity: 1;
  transform: translateY(0);
  transition: 1s ease;
}

.home .about-me-wrapper .about-me-container h2 {
  color: #eeeeee;
  border-bottom: 3px solid #eeeeee;
  font-size: 3em;
  margin: 0 auto;
  text-align: center;
}

.home .about-me-wrapper .about-me-container .content-wrapper {
  display: inline-block;
  float: left;
  margin: 2em 0;
  text-align: center;
  width: 100%;
}

@media screen and (min-width: 768px) {
  .home .about-me-wrapper .about-me-container .content-wrapper {
    width: 33.3%;
  }
}

.home .about-me-wrapper .about-me-container .icon-container {
  background-color: #333333;
  border-radius: 50%;
  display: table;
  height: 125px;
  margin: 0 auto;
  text-align: center;
  width: 125px;
}

.home .about-me-wrapper .about-me-container .icon-container i {
  color: #eeeeee;
  display: table-cell;
  font-size: 5em;
  vertical-align: middle;
}

.home .about-me-wrapper .about-me-container .text {
  color: #eeeeee;
  padding: 0 1em;
}

.home .about-me-wrapper .about-me-container .text h3 {
  margin: 0.33em 0;
}

.home .about-me-wrapper .about-me-container .text p {
  text-align: left;
}

.home .tools-container {
  left: 50%;
  margin-left: -50vw;
  position: relative;
  width: 100vw;
  background-color: #ffffff;
  opacity: 0;
  padding: 2em 0;
  transform: translateX(-30%);
}

.home .tools-container.show {
  opacity: 1;
  transform: translateX(0);
  transition: 1s ease;
}

.home .tools-container h2 {
  margin: 0 auto;
  max-width: 1440px;
  border-bottom: 5px solid #55accc;
  font-size: 3em;
  margin-bottom: 1em;
  text-align: center;
}

.home .tools-container .tool-images {
  padding: 0 1em;
}

.home .tools-container .tool-images .image-container {
  display: inline-block;
  margin: 2em 0;
  text-align: center;
  width: 49%;
}

@media screen and (min-width: 768px) {
  .home .tools-container .tool-images .image-container {
    width: 24%;
  }
}

.home .tools-container .tool-images .image-container img {
  height: auto;
  max-height: 250px;
  max-width: 100%;
  position: relative;
  vertical-align: middle;
}

@media screen and (min-width: 768px) {
  .blog:after, .archive:after {
    display: table;
    content: '';
    clear: both;
  }
}

.blog .banner, .archive .banner {
  left: 50%;
  margin-left: -50vw;
  position: relative;
  width: 100vw;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../images/yosemite.jpg");
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  display: table;
  height: 40vh;
  overflow: hidden;
}

.blog .banner .banner-title, .archive .banner .banner-title {
  animation: fade-in 1s ease 1s forwards;
  color: #eeeeee;
  display: table-cell;
  font-size: 12vw;
  opacity: 0;
  position: relative;
  text-align: center;
  vertical-align: middle;
}

@media screen and (min-width: 768px) {
  .blog .banner .banner-title, .archive .banner .banner-title {
    font-size: 6vw;
  }
}

@media screen and (min-width: 768px) {
  .blog .posts-container, .archive .posts-container {
    margin: 0 -1em;
    width: 73%;
  }
}

.blog .posts-container .post, .archive .posts-container .post {
  margin: 2em 0;
}

@media screen and (min-width: 768px) {
  .blog .posts-container .post, .archive .posts-container .post {
    display: inline-block;
    float: left;
    position: relative;
    width: 50%;
  }
}

@media screen and (min-width: 768px) {
  .blog .posts-container .post .post-content, .archive .posts-container .post .post-content {
    height: 100%;
    padding: 0 1em;
  }
}

@media screen and (min-width: 768px) {
  .blog .posts-container .post .post-image, .archive .posts-container .post .post-image {
    display: block;
  }
}

.blog .posts-container .post .post-image img, .archive .posts-container .post .post-image img {
  max-height: 100%;
  max-width: 100%;
}

@media screen and (min-width: 768px) {
  .blog .posts-container .post .post-text, .archive .posts-container .post .post-text {
    border-bottom: 1px solid #cccccc;
    display: block;
    padding-bottom: 3em;
    position: relative;
  }
}

.blog .posts-container .post .post-text .title, .archive .posts-container .post .post-text .title {
  font-size: 1.55em;
}

.blog .posts-container .post .post-text .title a, .archive .posts-container .post .post-text .title a {
  color: #333333;
}

.blog .posts-container .post .post-text .title a:hover, .archive .posts-container .post .post-text .title a:hover {
  color: #55accc;
}

.blog .posts-container .post .post-text .meta, .archive .posts-container .post .post-text .meta {
  font-size: 0.75em;
  margin: 0.25em 0;
}

.blog .posts-container .post .post-text .meta .divider, .archive .posts-container .post .post-text .meta .divider {
  margin: 0 0.5em;
}

.blog .posts-container .post .post-text .meta .post-categories, .archive .posts-container .post .post-text .meta .post-categories {
  display: inline-block;
}

.blog .posts-container .post .post-text .meta .post-categories li, .archive .posts-container .post .post-text .meta .post-categories li {
  display: inline;
  padding: 0 0.15em;
}

.blog .posts-container .post .post-text .content, .archive .posts-container .post .post-text .content {
  margin-bottom: 1em;
}

@media screen and (min-width: 768px) {
  .blog .posts-container .post .button-blue, .archive .posts-container .post .button-blue {
    bottom: 0;
    margin-bottom: 1em;
    position: absolute;
  }
}

.archive .banner {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../images/yosemite-valley.jpg");
  background-position: bottom;
}

#sidebar {
  border: 1px solid #333333;
  float: right;
  margin: 2em 0;
  width: 100%;
}

@media screen and (min-width: 768px) {
  #sidebar {
    width: 25%;
  }
}

@media screen and (min-width: 768px) {
  #sidebar .categories {
    background-color: #eeeeee;
  }
}

#sidebar .categories .title {
  background-color: #cccccc;
  border-bottom: 1px solid #333333;
  display: none;
  font-size: 1.5em;
  padding: 0.25em 0.5em;
}

@media screen and (min-width: 768px) {
  #sidebar .categories .title {
    display: block;
  }
}

#sidebar .categories .title.mobile {
  background-color: #eeeeee;
  border: none;
  display: block;
}

@media screen and (min-width: 768px) {
  #sidebar .categories .title.mobile {
    display: none;
  }
}

#sidebar .categories .title.mobile:hover {
  cursor: pointer;
}

#sidebar .categories .title.mobile .icon-container {
  background-color: #cccccc;
  border-radius: 50%;
  float: right;
  height: 35px;
  margin-top: 6px;
  right: 1em;
  transition: transform 0.4s ease;
  width: 35px;
}

#sidebar .categories .title.mobile .icon-container.open {
  transform: rotate(180deg);
  transition: transform 0.4s ease;
}

#sidebar .categories .title.mobile .icon-container .fa {
  display: inline-block;
  font-size: 0.7em;
  left: 50%;
  position: relative;
  top: 50%;
  transform: translate(-50%, -50%);
  vertical-align: top;
}

#sidebar .categories .categories-list {
  background-color: #e6e6e6;
  display: none;
  padding-bottom: 0.5em;
}

@media screen and (min-width: 768px) {
  #sidebar .categories .categories-list {
    background-color: transparent;
    display: block;
  }
}

#sidebar .categories .categories-list .category-item a {
  color: #55accc;
  display: block;
  padding: 0.5em 1em;
}

#sidebar .categories .categories-list .category-item a:hover {
  background-color: #cccccc;
  transition: all 0.4s ease;
}
